/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin, padding and set font inherit to all elements */
* {
  padding: 0;
  margin: 0;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Remove default underline from A elements */
a,
a:visited,
a:active {
  text-decoration: none;
}

/* Remove list styles on ul, ol elements */
ul,
ol {
  list-style: none;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Set core buttons and inputs defaults */
button,
[type=button],
[type=reset],
[type=submit] {
  border: 0;
  cursor: pointer;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --border-radius-1: 0.5rem;
}

.hidden {
  display: none !important;
}

:root {
  --ff-space-grotesk: "Space Grotesk", sans-serif;
  --fs-1: 0.688rem;
  --fs-2: 0.813rem;
  --fs-3: 1.125rem;
  --fs-4: 1.125rem;
  --fs-5: 1.75rem;
  --fw-4: 400;
  --fw-5: 500;
}
@media screen and (max-width: 19.99875em) {
  :root {
    --fs-1: 0.625rem;
    --fs-3: 1rem;
    --fs-4: clamp(0.938rem, 5.5vw, 1.125rem);
    --fs-5: 1.5rem;
  }
}
@media screen and (min-width: 64em) {
  :root {
    --fs-1: 0.875rem;
    --fs-2: 0.875rem;
    --fs-4: 1.75rem;
  }
}

body {
  font-family: var(--ff-space-grotesk);
  font-size: var(--fs-3);
  font-weight: var(--fw-5);
}

:root {
  --linear-gradient-1: linear-gradient(to right, hsl(249, 99%, 64%), hsl(278, 94%, 30%));
  --clr-red: hsl(0, 100%, 66%);
  --clr-white: hsl(0, 0%, 100%);
  --clr-violet-5: hsl(270, 3%, 87%);
  --clr-violet-7: hsl(279, 6%, 55%);
  --clr-violet-9: hsl(278, 68%, 11%);
}

.card {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--clr-white);
  text-transform: uppercase;
}
@media screen and (min-width: 64em) {
  .card {
    height: 100%;
  }
}
.card__wrapper {
  width: min(100%, 26.25rem);
  display: flex;
  justify-content: flex-end;
  position: relative;
  padding: 2rem 1rem 3.125rem;
}
@media screen and (max-width: 19.99875em) {
  .card__wrapper {
    justify-content: center;
  }
}
@media screen and (min-width: 48em) {
  .card__wrapper {
    width: 21.25rem;
    flex-direction: column;
    row-gap: 2.375rem;
    padding: 0;
  }
}
@media screen and (min-width: 64em) {
  .card__wrapper {
    width: 34.0625rem;
  }
}
.card .front,
.card .back {
  --card-width: 17.875rem;
  --card-height: 9.875rem;
  width: var(--card-width);
  height: var(--card-height);
  background: var(--bg-card-url) center/cover no-repeat;
  border-radius: 0.3125rem;
  box-shadow: 0.5rem 0.5rem 1.25rem rgba(0, 0, 0, 0.2);
}
@media screen and (max-width: 19.99875em) {
  .card .front,
.card .back {
    --card-width: 100%;
    --card-height: auto;
    aspect-ratio: 1.82/1;
  }
}
@media screen and (min-width: 48em) {
  .card .front,
.card .back {
    box-shadow: 0.375rem 0.5rem 1.25rem 0.25rem rgba(0, 0, 0, 0.12);
  }
}
@media screen and (min-width: 64em) {
  .card .front,
.card .back {
    --card-width: 28rem;
    --card-height: 15.375rem;
    border-radius: 0.625rem;
    box-shadow: 0.25rem 0.625rem 1.5625rem 0.625rem rgba(0, 0, 0, 0.11);
  }
}
.card .front {
  --bg-card-url: url("../images/bg-card-front.png");
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: -2.5rem;
  left: 1rem;
  padding: 1.125rem 1.25rem 1.25rem;
}
@media screen and (max-width: 19.99875em) {
  .card .front {
    --card-width: calc(100% - 2rem);
    left: auto;
    padding: 1.125rem;
  }
}
@media screen and (min-width: 48em) {
  .card .front {
    position: static;
  }
}
@media screen and (min-width: 64em) {
  .card .front {
    padding: 1.75rem 2rem;
  }
}
.card .front__logo {
  width: max-content;
  height: 1.875rem;
}
@media screen and (max-width: 19.99875em) {
  .card .front__logo {
    height: 1.625rem;
  }
}
@media screen and (min-width: 64em) {
  .card .front__logo {
    height: 2.9375rem;
  }
}
.card .front__infos {
  margin-block-start: auto;
}
.card .front__number {
  font-size: var(--fs-4);
  letter-spacing: 0.125em;
}
.card .front__wrapper {
  display: flex;
  justify-content: space-between;
  margin-block-start: 1.25rem;
  font-size: var(--fs-1);
  font-weight: var(--fw-4);
}
@media screen and (min-width: 64em) {
  .card .front__wrapper {
    margin-block-start: 2rem;
  }
}
.card .front__name {
  max-width: 24ch;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.card .back {
  --bg-card-url: url("../images/bg-card-back.png");
  padding-block-start: 4.125rem;
  padding-inline-end: 2.25rem;
  text-align: end;
}
@media screen and (max-width: 19.99875em) {
  .card .back {
    padding-block-start: 23%;
    padding-inline-end: 12.5%;
  }
}
@media screen and (min-width: 48em) {
  .card .back {
    align-self: flex-end;
  }
}
@media screen and (min-width: 64em) {
  .card .back {
    padding-block-start: 6.9375rem;
    padding-inline-end: 3.625rem;
  }
}
.card .back__cvc {
  font-size: var(--fs-1);
}

.form__container {
  display: flex;
  flex-direction: column;
  row-gap: 1.375rem;
  text-transform: uppercase;
}
@media screen and (min-width: 64em) {
  .form__container {
    row-gap: 1.75rem;
  }
}
.form__label {
  width: max-content;
  display: block;
  margin-block-end: 0.75rem;
  font-size: var(--fs-2);
  letter-spacing: 0.125em;
  color: var(--clr-violet-9);
}
.form__input {
  --clr-border: linear-gradient(var(--clr-violet-5), var(--clr-violet-5));
  width: 100%;
  padding: 0.625rem 0.9375rem;
  background: linear-gradient(var(--clr-white), var(--clr-white)) padding-box, var(--clr-border) border-box;
  border: 0.0625rem solid transparent;
  border-radius: var(--border-radius-1);
  font-size: var(--fs-3);
  color: var(--clr-violet-9);
}
.form__input::placeholder {
  color: var(--clr-violet-5);
}
.form__input:focus-visible {
  --clr-border: var(--linear-gradient-1);
  outline: none;
}
.form__input.invalid {
  --clr-border: linear-gradient(var(--clr-red), var(--clr-red));
}
.form__wrapper {
  display: flex;
  column-gap: clamp(0.75rem, 3vw, 1.25rem);
}
.form__date {
  border: none;
}
.form__date .form__wrapper {
  column-gap: clamp(0.5rem, 2vw, 0.625rem);
}
.form__date .form__input {
  width: clamp(4.5rem, 18vw, 5rem);
}
@media screen and (max-width: 19.99875em) {
  .form__date .form__input {
    width: 4.125rem;
  }
}
.form__cvc {
  width: 100%;
}
.form__submit {
  margin-block-start: 0.375rem;
}
@media screen and (min-width: 64em) {
  .form__submit {
    margin-block-start: 0.75rem;
  }
}
.form__error {
  display: inline-block;
  margin-block-start: 0.625rem;
  font-size: var(--fs-2);
  color: var(--clr-red);
  text-transform: none;
}

.button {
  width: 100%;
  position: relative;
  padding: 1.125rem;
  background-color: var(--clr-violet-9);
  border-radius: var(--border-radius-1);
  font-size: var(--fs-3);
  color: var(--clr-white);
  overflow: hidden;
  transition: background-color 250ms ease;
}
.button::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  background-image: var(--linear-gradient-1);
  z-index: -1;
  pointer-events: none;
}
.button:is(:hover, :focus-visible) {
  background-color: transparent;
}

.success {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.success h2 {
  font-size: var(--fs-5);
  text-transform: uppercase;
  letter-spacing: 0.125em;
  margin-block: 2.5rem 1.75rem;
}
.success p {
  margin-block-end: 3rem;
  font-size: var(--fs-3);
  color: var(--clr-violet-7);
}

.main {
  width: 100%;
  height: 100%;
}
.main__container {
  width: 100%;
  height: 100%;
  background: url("../images/bg-main-mobile.png") top left/100% 15rem no-repeat;
}
@media screen and (max-width: 19.99875em) {
  .main__container {
    background-size: 100% 75vw;
  }
}
@media screen and (min-width: 48em) {
  .main__container {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 4vw;
    background-image: url("../images/bg-main-desktop.png");
    background-size: 30% 100%;
  }
}
@media screen and (min-width: 64em) {
  .main__container {
    background-size: 36% 100%;
  }
}
@media screen and (min-width: 90em) {
  .main__container {
    column-gap: 7.875rem;
  }
}
.main__wrapper {
  width: min(100%, 27.5rem);
  padding: 5.875rem 1.5rem 3.125rem;
  margin-inline: auto;
}
@media screen and (max-width: 19.99875em) {
  .main__wrapper {
    padding-block-start: 5.25rem;
    padding-inline: 1rem;
  }
}
@media screen and (min-width: 48em) {
  .main__wrapper {
    width: min(45%, 23.875rem);
    padding: 0;
    margin-inline: 0;
  }
}